// 
export const getCategoryPage = (params: any) => {
  return request({
    url: '/category/page',
    method: 'get',
    params
  })
}

// 
export const queryCategoryById = (id: string | (string | null)[]) => {
  return request({
    url: `/category/${id}`,
    method: 'get'
  })
}

// 
export const deleCategory = (ids: string) => {
  return request({
    url: '/category',
    method: 'delete',
    params: { ids }
  })
}

// 
export const editCategory = (params: any) => {
  return request({
    url: '/category',
    method: 'put',
    data: { ...params }
  })
}

// 
export const addCategory = (params: any) => {
  return request({
    url: '/category',
    method: 'post',
    data: { ...params }
  })
}
